import AppScore from './AppScore.vue';
import AppManagement from '../../mixins/AppManagement.js';
import SvgFilterMixin from '../SvgFilterMixin.vue';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
export default {
  name: 'AppItem',
  components: {
    AppScore,
    NcButton
  },
  mixins: [AppManagement, SvgFilterMixin],
  props: {
    app: {},
    category: {},
    listView: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      isSelected: false,
      scrolled: false,
      screenshotLoaded: false
    };
  },
  computed: {
    hasRating() {
      return this.app.appstoreData && this.app.appstoreData.ratingNumOverall > 5;
    }
  },
  watch: {
    '$route.params.id'(id) {
      this.isSelected = this.app.id === id;
    }
  },
  mounted() {
    this.isSelected = this.app.id === this.$route.params.id;
    if (this.app.releases && this.app.screenshot) {
      const image = new Image();
      image.onload = e => {
        this.screenshotLoaded = true;
      };
      image.src = this.app.screenshot;
    }
  },
  watchers: {},
  methods: {
    async showAppDetails(event) {
      if (event.currentTarget.tagName === 'INPUT' || event.currentTarget.tagName === 'A') {
        return;
      }
      try {
        await this.$router.push({
          name: 'apps-details',
          params: {
            category: this.category,
            id: this.app.id
          }
        });
      } catch (e) {
        // we already view this app
      }
    },
    prefix(prefix, content) {
      return prefix + '_' + content;
    }
  }
};